import { getCompanyCreditQuery } from '@/checkout/api/getCompanyCredit/graphql/getCompanyCredit.graphql';
import { CompanyCredit } from '@/checkout/data/models';
import { transformCompanyCredit } from '@/checkout/data/transforms';
import { dispatchApiCall } from '@/checkout/lib/dispatchApiCall';

export const getCompanyCredit = async (): Promise<CompanyCredit | null> => {
  return await dispatchApiCall({
    type: 'query',
    query: getCompanyCreditQuery,
    options: { method: 'GET', cache: 'no-cache' },
    path: 'company.credit',
    transformer: transformCompanyCredit,
    defaultValueOnFail: null,
  });
};
