import { CompanyCredit as CompanyCreditModel } from '@/checkout/data/models';
import { CompanyCredit as CompanyCreditType } from '@/checkout/__generated__/types';

type OptionalCompanyCredit = CompanyCreditType | null | undefined;

const transformCompanyCredit = (data: OptionalCompanyCredit): CompanyCreditModel | null => {
  if (!data || !data?.available_credit || data.available_credit.value == null || !data.available_credit.currency) return null;

  const model = {
    availableCredit: {
      value: data.available_credit.value,
      currency: data.available_credit.currency,
    },
    exceedLimit: data?.exceed_limit || false,
  };

  return model;
};

export { transformCompanyCredit };
